import React from 'react';
import logo from '../Okapi_logo.svg'; 
import {
    Link
} from 'react-router-dom';
import { getUserId } from '../db/db';
import '../styles/components/Navbar.css';

function Blank(props) {
    return ( 
        <nav>
            <Link to="/"><img src={logo} alt="Okapi Logo"></img><span className="Tagline"> A place to share wisdom </span></Link>
        </nav>
    )
}

function LoggedIn(props) {
    return ( 
        <nav>
            <Link to="/"><img src={logo} alt="Okapi Logo"></img><span className="Tagline"> A place to share wisdom </span></Link>
            <div className="NavControls">
                {props.currentUser && 
                    <Link className="Username">
                        {props.currentUser.displayName}
                    </Link>}
                <button className="ButtonTertiary" href="" onClick={() => props.logout()}> Logout</button>
            </div>
        </nav>
    )
}

function LoggedOut(props) {
    return (
        <nav>
        <Link to="/"><img src={logo} alt="Okapi Logo"></img><span className="Tagline"> A place to share wisdom </span></Link>
        <div className="NavControls">
            <Link className="ButtonTertiary" to="/login">Login</Link> <br/>
        </div>
        </nav>
    )
}

function Navbar(props) {
    if(props.status === "blank") {
        return Blank(props);
    } else if(props.status === "loggedIn") {
        return LoggedIn(props);
    } else {
        return LoggedOut(props);
    }
}

export default Navbar;