import React, { Component } from 'react'
import Topic from '../components/Topic';
import { Form, Field } from 'react-final-form';
import { connect } from 'react-redux'
import { getTopicAnswers, saveTopicAnswer, getTopic } from '../db/db';
import { Container, Row, Col } from 'react-bootstrap';
import Answers from '../components/Answers';
import AnswerField from '../components/AnswerField';

import '../styles/pages/TopicPage.css';

const mapStateToProps = state => ({
  currentUser: state.user.currentUser
})
const mapDispatchToProps = dispatch => ({})

class TopicPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      submitting: false,
      submitAnswerText: "Submit Answer"
    };
    this.id = this.props.match.params.id;
    getTopic(this.id).then(topic => {
      this.setState({
        topic: topic
      });
    });
    getTopicAnswers(this.id).then(answers => {
      var showUserAnswerEdit;
      var showAnswerField;
      var submitAnswerText = "Submit Answer";
      console.log("user answer: ", userAnswer);
      if(!this.props.currentUser) {
        showUserAnswerEdit = false;
        showAnswerField = false;
      } else {
        var userAnswer = this.userAlreadyHasAnswer(answers);
        if (!userAnswer){
          showUserAnswerEdit = false;
          showAnswerField = true;
        } else {
          showUserAnswerEdit = true;
          showAnswerField = false;
          submitAnswerText = "Update Answer";
        }
      } 
      this.setState({
        showUserAnswerEdit: showUserAnswerEdit,
        showAnswerField: showAnswerField,
        userAnswer: userAnswer,
        submitAnswerText: submitAnswerText,
        answers: answers
      });
    })
  }

  onSubmit(values) {
    values.topicId = this.id;
    this.setState({
      submitting: true
    });
    saveTopicAnswer(values, this.props.currentUser)
      .then(done => {
        window.location.reload();
      })
      .catch(error => {
        console.log(error);
      });
  }

  validate() {
    return true;
  }

  userAlreadyHasAnswer(answers) {
    var userAnswer;
    console.log("answers: ", answers);
    console.log("current user: ", this.props.currentUser);
    answers.map(answer => {
      if(answer.data.user.uid == this.props.currentUser.uid) {
        userAnswer = answer;
      }
    });
    return userAnswer;
  }

  changeToAnswerField() {
    this.setState({
      showUserAnswerEdit: false,
      showAnswerField: true
    })
  }

  render() {
    if(!this.state.topic) {
      return <div className="TopicPage"></div>
    } else {
      return (
        <div className="TopicPage">
          <Container>
            <Row>
              <Col md={10} sm={12}>
                <div className="Topic">
                  <div className="TopicWrapper">
                    <h2 className="Title">{this.state.topic.data.title}</h2>
                    <p className="Description">{this.state.topic.data.description}</p>

                    {this.state.showAnswerField && 
                      <AnswerField 
                      answer={this.state.userAnswer}
                      onSubmit={(values) => this.onSubmit(values)} 
                      validate={this.validate} 
                      submitting={this.state.submitting}
                      submitText={this.state.submitAnswerText}/>
                    }

                    {this.state.showUserAnswerEdit &&
                      <div className="UserAnswerEdit">
                        <span> Your answer: </span>
                        <p>{this.state.userAnswer.data.body}</p>
                        <button className="button-secondary" type="submit" onClick={() => this.changeToAnswerField()}>Edit Answer</button>
                      </div>
                    }
                    {(this.state.showAnswerField || this.state.showUserAnswerEdit) && <hr/>}
                    {this.state.answers && <Answers answers={this.state.answers}/>}
                  </div>
                </div>
              </Col>
            </Row>
          </Container>
        </div>
      )
    }
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(TopicPage)
