import React from 'react';
import { Form, Field } from 'react-final-form';

function AnswerField(props) {
    return (
      <Form
      onSubmit={props.onSubmit}
      validate={props.validate}
      render={({handleSubmit, pristine, invalid}) => (
        <form onSubmit={handleSubmit}>
          <div className="AnswerInput">
            {props.answer ?
                <Field initialValue={props.answer.data.body} name="body" component="textarea" placeholder="Your answer..."></Field>
            :
                <Field name="body" component="textarea" placeholder="Your answer..."></Field>
            }
          </div>
          <button className="button" type="submit" disabled={pristine || invalid || props.submitting}>
            {props.submitText ? props.submitText : "Submit Answer"}
          </button>
        </form>
      )}/>
    )
  }
  
  export default AnswerField;