import React, { Component } from 'react';
import firebase from 'firebase';
import {
  BrowserRouter as Router,
  Route,
  Link
} from 'react-router-dom';
import { connect } from 'react-redux';
import { setCurrentUser } from './redux/actions/actions.js';

import HomePage from './pages/HomePage';
import LoginPage from './pages/LoginPage';
import UserPage from './pages/UserPage';
import TopicsPage from './pages/TopicsPage';
import TopicPage from './pages/TopicPage';
import Navbar from './components/Navbar';
import Layout from './layouts/Layout';

import './App.css';
import { saveUser } from './db/db.js';

var SpotifyApi = require('spotify-web-api-node');
var Spotify = new SpotifyApi({
  clientId: '57763b304f174d8b8b7d84236e776856',

});

const mapReduxStateToProps = state => ({
  currentUser: state.user.currentUser,
  signedIn: state.user.signedIn
});
const mapDispatchToProps = dispatch => ({
  setCurrentUser: (user) => dispatch(setCurrentUser(user)),
});

class App extends Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
    firebase.auth().onAuthStateChanged((user) => {
      if(user) {
        saveUser(user).then(done => {
          this.props.setCurrentUser(user);
        })
        .catch(error => {
          console.log(error);
        });
      } else {
        // user not signed in
        this.props.setCurrentUser(null); //doing this sets sign in to false
      }
    });
  }

  logout() {
    firebase.auth().signOut();
  }

  loadingScreen() {
      return (
          <Layout>
            <Navbar status="blank"></Navbar>
            Loading...
          </Layout>
      )
  }

  notSignedInScreen() {
    return (
        <Layout>
          <Navbar status="loggedOut"></Navbar>
          <LoginPage />
        </Layout>
    )
  }

  mainScreen() {
    return (
      <Layout>
          {this.props.currentUser ? 
            <Navbar logout={() => this.logout()} status="loggedIn" currentUser={this.props.currentUser}></Navbar>
          :  
            <Navbar logout={() => this.logout()} status="loggedOut"></Navbar>
          }

          <Route exact path="/login" component={LoginPage}/>
          <Route exact path="/topics" component={TopicsPage}/>
          <Route exact path="/topic/:id" component={TopicPage}/>
          <Route exact path="/user/:user_id" component={UserPage}/>
          <Route exact path="/" component={TopicsPage}/>
      </Layout>
    );
  }

  render() {
    if(this.props.signedIn === undefined) {
      return this.loadingScreen();
    } else if(!this.props.signedIn) {
      //return this.notSignedInScreen();
      return this.mainScreen();
    } else {
      return this.mainScreen();
    }
  }
}

/*
  This little work around is to allow the redux store to be passed to the
  'App' component.
  See: https://github.com/reactjs/react-redux/issues/390#issuecomment-221389608
*/
export default connect(
  mapReduxStateToProps,
  mapDispatchToProps
)(App);
