const userReducer = (state = {}, action) => {
  switch(action.type) {
    case 'SET_CURRENT_USER':
      var newState = {...state}
      newState.currentUser = action.currentUser
      if(action.currentUser) {
        newState.signedIn = true;
      } else {
        newState.signedIn = false;
      }
      return newState
    default:
      return state
  }
}

export {userReducer};
