import React from 'react';
import Answer from './Answer';
import Answers from './Answers';
import AnswerField from './AnswerField';
import '../styles/components/Topic.css';

function Topic(props) {
    return (
        <div className="Topic">
            <div className="TopicWrapper">
              <h2 className="Title">{props.title}</h2>
              <p className="Description">{props.description}</p>

              {props.showAnswerField && 
                <AnswerField 
                answer={props.userAnswer}
                onSubmit={props.onSubmit} 
                validate={props.validate} 
                submitting={props.submitting}/>
              }

              {props.showUserAnswerEdit &&
                <div className="UserAnswerEdit">
                  <p>{props.userAnswer.data.body}</p>
                  <button className="button-secondary" type="submit" onClick={props.changeToAnswerField}>Edit Your Answer</button>
                </div>
              }
              {(props.showAnswerField || props.showUserAnswerEdit) && <hr/>}
              {props.answers && <Answers answers={props.answers}/>}
            </div>
        </div>
    )
}

export default Topic;