/**
 * Extracts array of data from firebase querySnapshot
 * into a dictionary
 */
function extractData(doc) {
    return {
        id: doc.id,
        data: doc.data()
    }
}

/**
 * Extract data from firebase querySnapshot
 * into a dictionary
 */
function extractDataMultiple(querySnapshot) {
    var data = [];
    querySnapshot.forEach(doc => {
        data.push({
            id: doc.id,
            data: doc.data()
        });
    });
    return data;
}

export function getUserId(displayName, uid) {
    return displayName.split(" ").join("-") + "-" + uid.slice(0, 3);
}

/**
 * Save user to the database
 */
export function saveUser(userInfo) {
    return new Promise(function(resolve, reject) {
        db.collection('users').doc(userInfo.uid).set({
            photo_URL: userInfo.photoURL,
            email: userInfo.email,
            display_name: getUserId(userInfo.displayName, userInfo.uid)
        }, {merge: true})
        .then(function(docRef) {
            resolve(docRef);
        })
        .catch(function(error) {
            reject(error);
        });
    });
}

/**
 * Get the ids and data for all the topics
 */
export function getTopics() {
    return new Promise(function(resolve, reject) {
        db.collection("topics").get()
        .then(function(querySnapshot) {
            resolve(extractDataMultiple(querySnapshot));
        })
        .catch(function(error) {
            reject(error);
        });
    });
}

/**
 * Get the data for a topic
 */
export function getTopic(id) {
    return new Promise(function(resolve, reject) {
        db.collection("topics").doc(id).get()
        .then(function(querySnapshot) {
            resolve(extractData(querySnapshot));
        })
        .catch(function(error) {
            reject(error);
        });
    });
}

/**
 * Get the answers for a specific topic
 * @param {str} id 
 */
export function getTopicAnswers(id) {
    return new Promise(function(resolve, reject) {
        db.collection("answers")
        .where("topic_id", "==", id)
        .orderBy("created_at", "desc")
        .get()
        .then(function(querySnapshot) {
            resolve(extractDataMultiple(querySnapshot));
        })
        .catch(function(error) {
            reject(error);
        });
    });
}

/**
 * Save answers for a topic
 */
export function saveTopicAnswer(answer, user) {
    return new Promise(function(resolve, reject) {
        db.collection("answers").doc(user.uid + answer.topicId).set({
            body: answer.body,
            topic_id: answer.topicId,
            created_at: firebase.firestore.Timestamp.fromDate(new Date()),
            // store the user data in answer so we don't have to re
            // request user every time
            user: {
                photo_URL: user.photoURL,
                display_name: user.displayName,
                uid: user.uid
            }
        }, { merge: true})
        .then(function(docRef) {
            resolve(docRef);
        })
        .catch(function(error) {
            reject(error);
        });
    });
}
